<template>
  <header class="navbar">
    <Row>
      <Col span="24">
        <Form>
          <Form-item>
            <h5>{{textDic.title}}</h5>
          </Form-item>
          <Form-item>
            <div style="width: 1200px; height: 1px; background-color: #333;margin: 20px 0;"></div>
          </Form-item>
        </Form>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <header class="navbar">
        <span style="margin-right: 50px;">{{textDic.text0}}</span>
        <Input v-model="searchName" :placeholder="textDic.text0" style="width: 200px; margin-right: 50px;"/>
        <span style="margin-right: 50px;">{{textDic.text1}}</span>
        <Input v-model="searchName" :placeholder="textDic.text1" style="width: 200px; margin-right: 50px;"/>
        <Button type="primary" style="margin-right: 10px;">{{textDic.text2}}</Button>
        <Button type="primary">{{textDic.text3}}</Button>
      </header>
      </Col>
    </Row>
    <Row>
      <Col span="24">
        <header class="navbar addBtn">
          <Button type="primary" @click="didClickAdd">{{textDic.text4}}</Button>
        </header>
        <table border="1px" cellspacing="0" cellpadding="0">
          <tr>
            <th v-for="(title, i) in textDic.tableTitles" :key="i">{{title}}</th>
          </tr>
          <tr v-for="(formItem, index) in formItems" :key="index">
            <td v-for="(text, j) in formItem" :key="j">
              <custom-cell :index="index" :cellData="cellText(j, text)" :type="cellType(j)" @didClickButton="didClickButton"></custom-cell>
            </td>
          </tr>
        </table>
    <Modal
        v-model="modal"
        :title="textDic.text4"
        @on-ok="didClickModel(true)"
        @on-cancel="didClickModel(false)">
        <Form :label-width="80">
          <Form-item :label="textDic.editTitles[0]">
            <Input v-model="editForm[0]"/>
          </Form-item>
          <Form-item :label="textDic.editTitles[1]">
            <Input v-model="editForm[1]"/>
          </Form-item>
        </Form>
    </Modal>
      </Col>
    </Row>
    </header>
</template>

<script>
import CustomCell from '../components/CustomCell'
export default {
  components: {
    CustomCell
  },

  data() {
    return {
      formItems: [
        [1546247235000, '独孤求败', '13444444445', 'AW2312', '爽歪歪', ''],
        [1546247235000, '任我行', '13444444445', 'JE2345', '美滋滋', ''],
        [1546247235000, '平台', '13444444445', '0000', '管理员', ''],
      ],
      editForm: ['', ''],
      currentIndex: 0,
      modal: false
    }
  },

  methods: {
    cellText(j, text) {
      return j==5 ? [this.textDic.text5] : [text]
    },
    cellType(j) {
      if(j == 0) {
        return 'date';
      }else if(j == 5) {
        return 'button';
      }else {
        return 'text';
      }
    },
    didClickButton(data) {
      this.currentIndex = data.index;
      this.modal = true;
    },
    didClickAdd() {

    },
    didClickModel(isEdit) {

    }
  },
  computed:{
    textDic() {
      return {
        title: '代理商信息',
        tableTitles: ['添加时间', '姓名', '手机号', '推荐码', '添加人', '操作'],
        editTitles: ['手机号*', '真实姓名*'],
        text0: '姓名',
        text1: '手机号',
        text2: '搜索',
        text3: '重置',
        text4: '添加',
        text5: '编辑'
      }
    }
  }

}
</script>

<style>
.addBtn {
    background-color: #333;
    margin-top: 60px;
    padding: 5px 10px;;
  }
  td, th {
    width: 218px; 
    height: 60px;
  }
</style>



